<template>
  <div class="wbfilter">
    <div class="filterList">
      <div class="filterButton">
        <el-select
          v-model="searchList"
          size="mini"
          placeholder="è¯·éæ©åå²æ¥è¯¢æ¡ä»¶åè¡¨"
          @change="checkfilter"
        >
          <el-option
            v-for="item in filterSaveList"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
        <el-button size="mini" @click="filterNameMod = true"
          >ä¿å­æ¥è¯¢æ¡ä»¶</el-button
        >
        <el-button size="mini" @click="filterSearchList" type="primary"
          >æ¥è¯¢</el-button
        >
      </div>
      <div v-for="(item, index) in filterList" :key="index">
        <component
          :is="item.type"
          :data="item"
          :index="index"
          @delItem="delItem"
        ></component>
      </div>
    </div>
    <!-- å¼¹æ¡ -->
    <el-dialog title="æ°å¢ç­éæ¡ä»¶" :visible.sync="filterMod">
      <div>
        <el-tree
          :data="filterTree"
          show-checkbox
          default-expand-all
          node-key="column"
          ref="tree"
          @check="checkBox"
          highlight-current
          :props="defaultProps"
        >
        </el-tree>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="filterMod = false">å æ¶</el-button>
        <el-button type="primary" @click="addFilterBtn">ç¡® å®</el-button>
      </div>
    </el-dialog>
    <el-dialog title="è¾å¥ç­éå¨åç§°" :visible.sync="filterNameMod">
      <div>
        <el-input type="text" placeholder="è¾å¥åç§°" v-model="filterName" />
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="filterNameMod = false">å æ¶</el-button>
        <el-button
          type="primary"
          :disabled="!Boolean(filterName.length)"
          @click="saveFilter"
          >ç¡® å®</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
import String from './filterString.vue';
import Integer from './filterInteger.vue';
import Date from './filterDate.vue';
export default {
  name: 'wbfilter',
  data() {
    return {
      loading: true,
      filterMod: false,
      filterNameMod: false,
      filterName: '',
      filterTree: [],
      filterList: [],
      filterSaveList: [],
      filterItem: '',
      defaultProps: {
        children: 'childrenMenu',
        label: 'columnCn'
      },
      searchList: ''
    };
  },
  props: ['tableParameter', 'axios', 'tableHeader'],
  components: {
    String,
    Integer,
    Date
  },
  created() {
    this.getFilterAllList();
    this.getSaveFilterList();
  },
  methods: {
    // éæ©è¿æ»¤å¨
    checkfilter(val) {
      console.log(val);
    },
    // è·åç­éä¸ææ¡çåè¡¨
    getSaveFilterList() {
      this.axios({
        method: this.$parent.tableParameter.filterSaveList?.methods,
        url: `${this.$parent.tableParameter.filterSaveList?.url}?className=${this.$parent.tableParameter.saveFilter?.pageCode}`,
        showMag: true
      }).then(res => {
        if (res.data.code == '200') {
          this.filterSaveList = res.data.data;
        }
      });
    },
    // ä¿å­
    saveFilter() {
      this.axios({
        method: this.$parent.tableParameter.saveFilter?.methods,
        url: `${this.$parent.tableParameter.saveFilter?.url}`,
        showMag: true,
        data: {
          fieldInfoResponses: this.filterList,
          name: this.filterName,
          className: this.$parent.tableParameter.saveFilter?.pageCode,
          userCode: window.localStorage.getItem('userCode')
        }
      }).then(res => {
        if (res.data.code == '200') {
          this.filterNameMod();
          this.getSaveFilterList();
        }
      });
    },
    // æ¥è¯¢æé®
    filterSearchList() {
      this.$parent.getTableList();
    },
    // å é¤æ¡ä»¶
    delItem(index) {
      this.filterList.splice(index, 1);
    },
    // éä¸­åè°
    checkBox(data) {
      this.$refs.tree.setCheckedKeys([], false);
      this.$refs.tree.setCheckedKeys([data.column], false);
      this.filterItem = data;
    },
    // ç­éæ¡ä»¶åè¡¨
    filterTreeMod() {
      this.filterItem = '';
      this.filterMod = true;
      this.$nextTick(() => {
        this.$refs.tree.setCheckedKeys([], false);
      });
    },
    addFilterBtn() {
      this.filterList.push(this.filterItem);
      this.filterMod = false;
    },
    // è·ååè¡¨
    getFilterAllList() {
      this.axios({
        method: this.$parent.tableParameter.filterAllList?.methods,
        url: `${this.$parent.tableParameter.filterAllList?.url}?className=${this.$parent.tableParameter.filterAllList?.pageCode}`
      }).then(res => {
        if (res.data.code == '200') {
          this.filterTree = res.data.data;
        }
      });
    }
  }
};
</script>

